import { useAuth } from "../context/AuthContext";
import { db } from '../config/firebase';
import { ref, set, query, orderByChild, equalTo, get } from 'firebase/database';
import { PredictionProps } from "../interfaces";
import Moment from 'react-moment';
import 'moment-timezone';
import { useMatches } from '../context/MatchesContext';
import React, { useState } from 'react';

const PredictionItem = (match: PredictionProps) => {
  const user = useAuth()?.user;

  const allMatches = useMatches();

  const [selectedHome, setSelectedHome] = useState<string>('');
  const [selectedAway, setSelectedAway] = useState<string>('');

  // Function to generate image source
  const generateImageSrc = (selectedHome: string) => {
    if (selectedHome && selectedHome !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${selectedHome}.png`;
    } else if (match.home !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${match.home}.png`;
    } else {
      // If selectedHome is empty, return a default image or any fallback image you prefer
      return 'data:image/png;base64,';
    }
  };


  // Function to generate image source
  const generateImageSrcAway = (selectedAway: string) => {
    if (selectedAway && selectedAway !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${selectedAway}.png`;
    } else if (match.away !== "NNB") {
      return `https://img.uefa.com/imgml/flags/50x50/${match.away}.png`;
    } else {
      // If selectedHome is empty, return a default image or any fallback image you prefer
      return 'data:image/png;base64,';
    }
  };

  const handleHomeChange = async (eventHome: React.ChangeEvent<HTMLSelectElement>) => {
    const teamCode = eventHome.target.value;
    const selectedHomeName: string = eventHome.target.options[eventHome.target.selectedIndex].text || ''; // Use selectedIndex to get the selected option's text

    try {
      // Construct the database reference for home prediction and home team prediction
      const homePredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/homeNamePrediction`);
      const homeTeamPredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/homeTeamPrediction`);

      // Update the prediction with the selected home name and home team code in the database
      await Promise.all([
        set(homePredictionRef, selectedHomeName),
        set(homeTeamPredictionRef, teamCode)
      ]);

      setSelectedHome(selectedHomeName);
    } catch (error) {
      console.error("Error updating prediction:", error);
      // Handle error if needed
    }
  };

  const handleAwayChange = async (eventAway: React.ChangeEvent<HTMLSelectElement>) => {
    const teamCode = eventAway.target.value;
    const selectedAwayName: string = eventAway.target.options[eventAway.target.selectedIndex].text || ''; // Use selectedIndex to get the selected option's text

    try {
      // Construct the database reference for home prediction and home team prediction
      const homePredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/awayNamePrediction`);
      const homeTeamPredictionRef = ref(db, `predictions/${user?.uid}/${match.id}/awayTeamPrediction`);

      // Update the prediction with the selected home name and home team code in the database
      await Promise.all([
        set(homePredictionRef, selectedAwayName),
        set(homeTeamPredictionRef, teamCode)
      ]);

      setSelectedAway(selectedAwayName);
    } catch (error) {
      console.error("Error updating prediction:", error);
      // Handle error if needed
    }
  };

  // Ensure allMatches is not null before proceeding
  if (!allMatches) {
    return null; // Or handle the null case in a different way
  }
  // Filter allMatches to include only items where matchItem.group ends with the same character as match.homeName
  const filteredHomeMatches = allMatches.filter(matchItem => matchItem.group.endsWith(match.homeName.charAt(match.homeName.length - 1)) &&
    matchItem.homeName.length > 4);

  // Filter unique home names and home values
  const uniqueHomeNamesAndValues = filteredHomeMatches.reduce(
    (acc, matchItem) => {
      if (
        matchItem.homeName.length >= 4 &&
        !matchItem.homeName.includes('/') &&
        !acc.names.includes(matchItem.homeName)
      ) {
        acc.names.push(matchItem.homeName);
        acc.values.push(matchItem.home);
      }
      return acc;
    },
    { names: [] as string[], values: [] as string[] }
  );

  // Filter matches based on the conditions for awayName
  const filteredAwayMatches = allMatches.filter(matchItem => {
    // Check if match.awayName contains a slash
    if (match.awayName.includes('/')) {
      // Split match.awayName by slash and iterate over each part
      const parts = match.awayName.split('/');
      return parts.some(part =>
        matchItem.group && matchItem.group.endsWith(part.charAt(part.length - 1))
      );
    } else {
      // Check if match.awayName meets the original condition
      return (
        (match.awayName.length < 4 || match.awayName.includes('/')) &&
        matchItem.group &&
        matchItem.group.endsWith(match.awayName.charAt(match.awayName.length - 1))
      );
    }
  });

  const uniqueAwayNamesAndValues = filteredAwayMatches.reduce(
    (acc, matchItem) => {
      if (
        matchItem.awayName.length >= 4 &&
        !matchItem.awayName.includes('/') &&
        !acc.names.includes(matchItem.awayName)
      ) {
        acc.names.push(matchItem.awayName);
        acc.values.push(matchItem.away);
      }
      return acc;
    },
    { names: [] as string[], values: [] as string[] }
  );

  const homePredictionHandler = (eventHome: React.ChangeEvent<HTMLInputElement>) => {
    set(ref(db, `predictions/${user?.uid}/${match.id}/homePrediction`), eventHome.target.value);
  };

  const awayPredictionHandler = (eventAway: React.ChangeEvent<HTMLInputElement>) => {
    set(ref(db, `predictions/${user?.uid}/${match.id}/awayPrediction`), eventAway.target.value);
  };

  const isEditable = () => {
    let twoHours = 3600;  // One hour before
    let now = Math.floor(new Date().getTime() / 1000);
    return match.timestamp - now > twoHours;
  }

  return (
    <div className="py-4 px-8 bg-white shadow-lg rounded-lg my-2">
      <table className="w-full border-separate [border-spacing:0.75rem]">
        <tbody>
          <tr>
            <td className="w-10"><img className="w-12" alt={selectedHome ? selectedHome : match.home} src={generateImageSrc(match.homeTeamPrediction ? match.homeTeamPrediction : match.home)} /></td>
            <td className="text-xl">
              {/* Render select input if not Groepsfase */}
              {match.round.toString() !== "Groepsfase" ? (
                <>
                  {/* Render select input */}
                  {isEditable() && match.uid === user?.uid ? (
                    <select id={`home-${match.game}`} value={selectedHome} onChange={handleHomeChange}>
                      {/* Pick option based on predictions filled in */}
                      <option value={match.homeTeamPrediction ? `${match.homeTeamPrediction}` : `${match.home}`}>
                        {match.homeNamePrediction ? `${match.homeNamePrediction}` : `Kies een team`}
                      </option>
                      {/* Map over uniqueHomeNamesAndValues to generate options, excluding selectedHome and match.homeTeamPrediction values */}
                      {uniqueHomeNamesAndValues.values.map((home, index) => {
                        // Check if the round is not "Achtste finales"
                        if (match.round.toString() === "Achtste finales") {
                          return (
                            <option key={index} value={home}>
                              {uniqueHomeNamesAndValues.names[index]} {/* Using homeName here */}
                            </option>
                          );
                        } else {
                          const indexFromHomeName = match.homeName.slice(-2);
                          const homeSelect = document.getElementById(`home-${indexFromHomeName}`) as HTMLSelectElement;
                          const awaySelect =document.getElementById(`away-${indexFromHomeName}`) as HTMLSelectElement;
                          console.log(homeSelect);

                          const homeOptions = homeSelect ? Array.from(homeSelect.children) : [];
                          const awayOptions = awaySelect ? Array.from(awaySelect.children) : [];

                          const combinedOptions = [...homeOptions, ...awayOptions].map((option, index) => (
                            <option key={index} value={(option as HTMLOptionElement).value}>
                              {(option as HTMLOptionElement).textContent}
                            </option>
                          ));

                          return combinedOptions;

                        }
                      })}
                    </select>
                  ) : (
                    <span>{match.homeNamePrediction ? match.homeNamePrediction : '--'}</span>
                  )}
                </>
              ) : (
                match.homeName
              )}
            </td>
            {match.round.toString() !== "Groepsfase" && (
              <td className="w-10">{match.homeName}</td>
            )}
            {match.round.toString() === "Groepsfase" && (
              <React.Fragment>
                <td className="text-right">
                  {isEditable() && match.uid === user?.uid && (
                    <input
                      type="number"
                      value={match.homePrediction! > -1 ? match.homePrediction : ''}
                      onChange={homePredictionHandler}
                      className="w-16 shadow appearance-none border rounded py-1 px-2 text-gray-700 leading-tight text-center outline-none"
                    />
                  )}
                  {(!isEditable() || match.uid !== user?.uid) && (
                    <span className="border p-2 rounded-md bg-gray-100">{match.homePrediction! > -1 ? match.homePrediction : '-'}</span>
                  )}
                </td>
              </React.Fragment>
            )}
            <td className="text-right">
              <>{match.homeScore! > -1 ? match.homeScore : <span className="text-gray-300">--</span>}</>
            </td>
            <td rowSpan={2} className="text-right w-10">
              {match.points! > -1 ? <div className="text-xl text-center rounded-full text-amber-900 bg-amber-300 p-1">{match.points}</div> : <span className="text-gray-300">--</span>}
            </td>
          </tr>
          <tr>
            <td className="w-10"><img className="w-12" alt={selectedAway ? selectedAway : match.away} src={generateImageSrcAway(match.awayTeamPrediction ? match.awayTeamPrediction : match.away)} /></td>
            <td className="text-xl">
              {/* Render select input if match.awayName length is less than 4 and allMatches is not null */}
              {match.round.toString() !== "Groepsfase" ? (
                <>
                  {/* Render select input */}
                  {isEditable() && match.uid === user?.uid ? (
                    <div>
                      <select id={`away-${match.game}`} value={selectedAway} onChange={handleAwayChange}>
                        {/* Pick option based on predictions filled in */}
                        <option value={match.awayTeamPrediction ? `${match.awayTeamPrediction}` : `${match.away}`}>
                          {match.awayNamePrediction ? `${match.awayNamePrediction}` : `Kies een team`}
                        </option>
                        {/* Map over uniqueHomeNamesAndValues to generate options, excluding selectedHome and match.awayTeamPrediction values */}
                        {uniqueAwayNamesAndValues.values
                          .map((away, index) => (
                            <option key={index} value={away}>
                              {uniqueAwayNamesAndValues.names[index]} {/* Using awayName here */}
                            </option>
                          ))}
                      </select>
                    </div>
                  ) : (

                    <span>{match.awayNamePrediction ? match.awayNamePrediction : '--'}</span>
                  )}
                </>
              ) : (
                <span>{match.awayName}</span>
              )}
            </td>
            {match.round.toString() !== "Groepsfase" && (
              <td className="w-10">{match.awayName}</td>
            )}
            {match.round.toString() === "Groepsfase" && (
              <React.Fragment>
                <td className="text-right">
                  {isEditable() && match.uid === user?.uid && (
                    <input
                      type="number"
                      value={match.awayPrediction! > -1 ? match.awayPrediction : ''}
                      onChange={awayPredictionHandler}
                      className="w-16 shadow appearance-none border rounded py-1 px-2 text-gray-700 leading-tight text-center outline-none"
                    />
                  )}
                  {(!isEditable() || match.uid !== user?.uid) && (
                    <span className="border p-2 rounded-md bg-gray-100">{match.awayPrediction! > -1 ? match.awayPrediction : '-'}</span>
                  )}
                </td>
              </React.Fragment>
            )}
            <td className="text-right">
              <>{match.awayScore! > -1 ? match.awayScore : <span className="text-gray-300">--</span>}</>
            </td>
          </tr>
          <tr>
            <td colSpan={5} className="text-xs text-gray-700">
              <>{match.group! !== "N/A" ? match.group : match.round} </>
              · {match.location} · <Moment format="HH:mm">{match.date}</Moment>
            </td>
          </tr>
        </tbody>
      </table>
    </div >
  )
}

export default PredictionItem;